.custom-shape-divider-bottom-1630974339 {
    position: relative;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.custom-shape-divider-bottom-1630974339 svg {
    position: relative;
    display: block;
    width: calc(127% + 1.3px);
    height: 278px;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.custom-shape-divider-bottom-1630974339 .shape-fill {
    fill: #1477C9;
}

/** For mobile devices **/
@media (max-width: 767px) {
    .custom-shape-divider-bottom-1630974339 svg {
        width: calc(127% + 1.3px);
        height: 207px;
    }
}



.custom-shape-divider-top-1630976015 {
